          color: $white;
          background: rgb(27, 27, 27);
          border: none;
          outline: none;
          padding: 10px;
          border-radius: 3px;
          margin: 10px 0;
          width: 100%;
          position: relative;
        }

        input[type="datetime-local"] {
          display: inline;
          color: $white;
          background: rgb(27, 27, 27);
          border: none;
          outline: none;
          padding: 10px;
          border-radius: 3px;
          width: 100%;
          font-family: "poppins", sans-serif;
          margin-top: -40px;

          &::-webkit-calendar-picker-indicator {
            filter: invert(100%);
          }

          &::after {
            content: url("../images/chevron-down-circle-outline.svg");
            background-color: $white;
            color: $white !important;
          }
        }

        input[type="radio"] {
          accent-color: $secondaryColor;

