            background: rgb(27, 27, 27);
            border: none;
            outline: none;
            padding: 10px;
            border-radius: 3px;
            width: 100%;
            margin-bottom: 30px !important;

            &::-webkit-calendar-picker-indicator {
              filter: invert(100%);
            }
          }
        }
      }

      .subscribe {
        display: none;
        h3 {
          font-size: 1rem;
          color: $bgColor;
          font-weight: 600;
        }
        .booking__type {
          #dis__able {
            opacity: 0.1;
            filter: alpha(opacity=50);
          }
          width: 100%;
          color: $white;
          background: rgb(27, 27, 27);
          outline: none;
          padding: 10px;
          border-radius: 3px;
          border: none;
          margin-top: 10px;
        }

        .booking__type1 {
          width: 100%;
          color: $white;
          background: rgb(27, 27, 27);
          outline: none;
          padding: 10px;
          border-radius: 3px;
          border: none;
          margin-top: 10px;
        }
        .time {
          input {
            display: inline;
            color: $white;
            background: rgb(27, 27, 27);
            border: none;
            outline: none;
            padding: 10px;
            border-radius: 3px;
            margin-top: 10px;
            width: 100%;
            font-family: "poppins", sans-serif;

            &::placeholder {
              font-weight: 400;
              color: $white;

              &::webkit-calendar-picker-indicator {
                content: url(./assests/icon/chevron-down-circle-outline.svg);
              }
            }

            &::-webkit-calendar-picker-indicator {
              filter: invert(100%);
            }
