        width: 50%;
        margin-left: 10px;
      }
    }

    button {
      background-color: $secondaryColor;
      color: $white;
      border: none;
      outline: none;
      border-radius: 5px;
      padding: 10px;
      width: 100%;
      margin-top: 40px;
    }

    p {
      text-align: center;
      margin-top: 10px;

      a {
        color: $white;
        text-decoration: underline;
        padding-top: 5px;

        &:hover {
          color: $secondaryColor;
        }
      }
    }
  }

  //images con section
  .img_con {
    .images__container {
      display: flex;
      justify-content: flex-start;
      align-items: centre;

      div {
        .fa {
          position: relative;
          left: 90px;
          top: 40px;
          background-color: $primary1;
          color: $white;
          padding: 10px;
          border-radius: 50%;
          height: 40px;
          width: 40px;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        .img {
          border-radius: 50%;
          object-fit: cover;
          width: 6rem;
          margin-bottom: 20px;
          margin-left: 20px;
        }
      }
    }

    h1 {
      color: $white;
      font-weight: 600;
      font-size: rem;

      span {
        color: $black;
      }
    }

    h4 {
      color: $white;
