          position: relative;
          top: 25px;
          border: 1px solid $black;
          padding: 5px;
          cursor: pointer;

          &:hover {
            background-color: rgba($color: #000000, $alpha: 0.15);
          }
        }
        /*  display: flex;
        align-items: center;
        justify-content: flex-start;
         */

        h3 {
          padding: 0 20px 0 20px;
          text-align: center;
          font-weight: 800;
          justify-self: center;
          align-self: center;
          font-size: 1.25rem;
        }
      }

      .d-flex__1 {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .input__sign__1 {
          width: 50%;
          margin-right: 20px;

          input[type="text"] {
            width: 100%;
          }
        }

        .input__sign__2 {
          width: 50%;

          input[type="text"] {
            width: 100%;
          }
        }
      }

      p {
        text-align: center;
        font-size: small;
      }

      label {
        display: block;
        margin-top: 20px;

        /*  &:nth-of-type(6){
          display: inline;
        } */
      }

      input[type="text"] {
        width: 100%;
        padding: 10px;
        outline: none;
        border: 1px solid $secondaryColor;
        border-radius: 10px;
        // pointer-events: ;
      }

      input[type="password"] {
        width: 100%;
        padding: 10px;
        outline: none;
        border: 1px solid $secondaryColor;
        border-radius: 10px;
      }

      span {
        color: $error;
        font-size: 14px;
