  background-image: linear-gradient(rgba($color: $black, $alpha: 0.89), rgba($color: $black, $alpha: 0.89)),
    url("../images/course-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
  overflow: hidden;

  .heading_container {
    padding-left: 90px;

    h2 {
      text-transform: capitalize;
    }

    .text-center {
      text-transform: capitalize;
      color: $white;
    }
  }

  .course_container {
    display: flex;
    margin-top: 35px;
    padding-left: 85px;
    position: relative;

    .box {
      margin: 0 10px;
      background-color: $white;
      color: $black;

      .img-box {
        position: relative;

        img {
          width: 100%;
        }
      }

      .detail-box {
        padding: 15px;

        h4 {
          font-weight: 600;
        }

        p {
          font-size: 15px;
        }

        a {
          @include hero_btn($secondaryColor, $white, 7px, 30px, 10px);
        }
      }
    }
  }

  .owl-carousel {
    position: unset;

    .item {
      width: 350px;
    }

    .owl-nav {
      position: absolute;
      top: 23%;
      left: 0;
      display: flex;
      flex-direction: column;

      button.owl-next,
      button.owl-prev {
        width: 50px;
        height: 50px;
        margin: 3px 0;
        border-radius: 100%;
        outline: none;
        background-color: $secondaryColor;
        color: $white;
        font-size: 20px;

        &:hover {
          background-color: $white;
          color: $black;
        }

        span {
          display: none;
        }
      }
    }

    .owl-dots {
      display: none;
    }
  }
}

// end route section

