  .heading_container {
    align-items: center;

    h2 {
      text-transform: capitalize;
    }
  }

  .box {
    display: flex;
    align-items: center;
    margin: 45px;
    padding: 35px 25px;
    box-shadow: 0 0 15px 0 rgba($color: #000000, $alpha: 0.15);

    .img-box {
      border-radius: 100%;
      border: 10px solid $secondaryColor;
      margin-right: 25px;
      min-width: 175px;
      max-width: 175px;
      overflow: hidden;

      img {
        width: 100%;
      }
    }

    .detail-box {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      .name {
        text-align: center;

        img {
          width: 25px;
          margin-bottom: 5px;
        }

        h6 {
          color: $primary1;
          font-size: 20px;
        }
      }
    }
  }

  .carousel_btn-container {
    display: flex;
