  }

  .carousel-control-prev,
  .carousel-control-next {
    position: unset;
    width: 45px;
    height: 45px;
    border: none;
    opacity: 1;
    background-repeat: no-repeat;
    background-size: 12px;
    background-position: center;
    background-color: $secondaryColor;
    background-position: center;
    border-radius: 100%;
    margin: 0 2.5px;

    &:hover {
      background-color: $black;
    }
  }

  .carousel-control-next {
    left: initial;
  }
}

