      height: 50px;
      margin-bottom: 25px;
      padding-left: 15px;
      outline: none;
      color: #101010;
      box-shadow: 0 0 5px 0 rgba($color: #000000, $alpha: 0.15);

      &::placeholder {
        color: #333;
      }

      &.message-box {
        height: 120px;
      }
    }

    button {
